@charset "UTF-8";
html {
  box-sizing: border-box; }

*, *::after, *::before {
  box-sizing: inherit; }

@font-face {
  font-family: "icon-font";
  src: url("/fonts/icon-font.eot");
  src: url("/fonts/icon-font.eot?#iefix") format("eot"), url("/fonts/icon-font.woff2") format("woff2"), url("/fonts/icon-font.woff") format("woff"), url("/fonts/icon-font.ttf") format("truetype"), url("/fonts/icon-font.svg#icon-font") format("svg"); }

.icon-facebook:before, .icon-instagram:before, .icon-twitter:before {
  font-family: "icon-font";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-style: normal;
  font-variant: normal;
  font-weight: normal;
  text-decoration: none;
  text-transform: none; }

.icon:before {
  font-family: "icon-font";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-style: normal;
  font-variant: normal;
  font-weight: normal;
  text-decoration: none;
  text-transform: none; }

.icon-facebook:before {
  content: ""; }

.icon-instagram:before {
  content: ""; }

.icon-twitter:before {
  content: ""; }
